
function Draw(obj) {
  this.obj=obj;
  this.init();
}
Draw.prototype.init=function () {
  let rules=[
    [0,0,-50], [50,0,-50], [100,0,-50],
    [0,50,-50], [50,50,-50], [100,50,-50],
    [0,100,-50], [50,100,-50], [100,100,-50],

    [0,0,0], [50,0,0], [100,0,0],
    [0,50,0],          [100,50,0],
    [0,100,0], [50,100,0], [100,100,0],

    [0,0,50], [50,0,50], [100,0,50],
    [0,50,50], [50,50,50], [100,50,50],
    [0,100,50], [50,100,50], [100,100,50],
  ]
  let values={
    a:{
      color:'white',tx:0,ty:0,tz:25,
      rx:0,ry:0
    },
    b:{
      color:'#ff2737',tx:0,ty:25,tz:0,
      rx:-90,ry:0
    },
    c:{
      color:'yellow',tx:0,ty:0,tz:-25,
      rx:-180,ry:0
    },
    d:{
      color:'#ffa109',tx:0,ty:-25,tz:0,
      rx:90,ry:0
    },
    e:{
      color:'#49da76',tx:-25,ty:0,tz:0,
      ry:-90,rx:0
    },
    f:{
      color:'#4959c2',tx:25,ty:0,tz:0,
      ry:90,rx:0
    }
  };
  switch (this.obj.layer) {
    case 3:
      this.bigWidth=(this.obj.width+2)*3;
      this.many=26;
      break;
    case 2:
      this.bigWidth=(this.obj.width+2)*3;
      this.many=4;
      break;

  }
  // this.many
  for(let i=0;i<26;i++){
    let div=document.createElement('div');
    let ds=div.style;
    div.className='divs'
    ds.height=this.bigWidth+'px';
    ds.width=this.bigWidth+'px';
    ds.position='absolute';
    ds.transformStyle='preserve-3d';
    ds.transform=`rotateX(0deg) rotateY(0deg) rotateZ(0deg)`;
    ds.transition='all .3s'

    let didDiv=document.createElement('div');
    let didds=didDiv.style;
    didds.position='absolute';
    didds.transformStyle='preserve-3d';
    didds.width='50px';
    didds.height='50px';
    didds.transform=`translateX(${rules[i][0]}px)
                     translateY(${rules[i][1]}px)
                     translateZ(${rules[i][2]}px)`
    for(let i in values){
      let span=document.createElement('div');
      let ss=span.style;
      ss.height=this.obj.height+'px';
      ss.width=this.obj.width+'px';
      ss.border=this.obj.border;
      ss.position='absolute';
      ss.borderRadius=this.borderRadius;
      ss.backgroundColor=values[i].color;
      ss.transform=`translateX(${values[i].tx}px)
                    translateY(${values[i].ty}px)
                    translateZ(${values[i].tz}px)
                    rotateX(${values[i].rx}deg)
                    rotateY(${values[i].ry}deg)
                    `;
      didDiv.appendChild(span);
    }
    div.appendChild(didDiv);
    this.obj.obj.appendChild(div);
   // console.log(this.obj);
  }
}

export default Draw


